import { createRouter, createWebHistory } from "vue-router";
import store from "../store";
import Home from "../components/HomeC.vue";
import Tests from "../components/TestsC.vue";
import HelloWorld from "../components/HelloWorld.vue";
import Login from "../components/Login.vue";
import Menu from "../components/MenuC.vue";
//import NewDate from "../components/NewDate.vue";

const routes = [
  {
    path: "/",
    redirect: "/home",
  },
  {
    path: "/home",
    name: "Home",
    component: Home,
    meta: { requiresAuth: true },
  },
  {
    path: "/login",
    name: "Login",
    component: Login,
    beforeEnter: (to, from, next) => {
      if (store.getters.user != null) {
        next({ name: "home" });
      } else {
        next();
      }
    },
  },
  {
    path: "/hello",
    name: "HelloWorld",
    component: HelloWorld,
  },
  {
    path: "/tests",
    name: "Tests",
    component: Tests,
  },
  {
    path: "/menu",
    name: "Menu",
    component: Menu,
  },
//   {
//     path: "/newDate",
//     name: "NewDate",
//     component: NewDateComponent,
//   },
  {
    path: "/:catchAll(.*)",
    redirect: "/login",
  },
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

router.beforeEach((to, from, next) => {
  // si la ruta requiere autenticación, verifica si el usuario está autenticado
  if (to.matched.some((record) => record.meta.requiresAuth)) {
    console.log(store);
    console.log("this.$store.getters.user", store.getters.user);
    // si el usuario no está autenticado, redirige a la página de login
    if (store.getters.getUser == null) {
      next({
        path: "/login",
        query: { redirect: to.fullPath },
      });
    } else {
      next(); //usuario autenticado, permite acceder a la ruta
    }
  } else {
    next(); //  ruta sin autenticación, permite acceder a la ruta
  }
});

export default router;
