<!-- @format -->

<template>
    <div id="dateList">
      <h2>Dates List</h2>
      <div v-if="newDates.length > 0" class="rowContainer">
        <div class="item-list">
          <ul>
            <li v-for="item in newDates" :key="item.id" class="item">
              <button
                class="secondary"
                :class="{ active: item === selectedItem }"
                @click="selectItem(item)"
              >
                {{ item.date }}
              </button>
            </li>
          </ul>
        </div>
        <div class="item-details">
          <div v-if="selectedItem">
            <h2>{{ selectedItem.enfermedad }}</h2>
            <p>{{ selectedItem.valoracionEspecialista }}</p>
            <button @click="deleteItem(selectedItem.id)" class="secondary">
              Delete
            </button>
          </div>
          <div v-else>
            <h2>Select an item from the list to see more details.</h2>
          </div>
        </div>
      </div>
      <p v-else>Loading...</p>
    </div>
  </template>
  
  <script>
  import { mapState, mapMutations } from "vuex";
  import axios from "axios";
  
  const url =
    "https://run.mocky.io/v3/5d505373-3718-4f3b-8a8c-839d47ae4d6b?mocky-delay=2000ms";
  
  export default {
    name: "ListC",
    computed: {
      ...mapState(["dates"]),
    },
    data() {
      return {
        selectedItem: null,
        newDates: [],
      };
    },
    methods: {
      ...mapMutations(["selectItem"]),
      selectItem(item) {
        this.selectedItem = item;
      },
      deleteItem(id) {
        //TODO - delete with axios request
        this.newDates = this.newDates.filter((item) => item.id !== id);
      },
    },
    created() {
      axios
        .post(url, {
          idUser: 1,
        })
        .then((response) => {
          console.log("Response data :" + response.data);
  
          // const responseData = response.data;
          // const dates = JSON.parse(responseData).dates; //responseData.dates; // or
          // console.log(dates); // This should log the "dates" array
  
          this.newDates = response.data.dates;
        });
    },
  };
  </script>
  
  <style lang="scss">
  #dateList {
    width: 100%;
  
    .rowContainer {
      border: 1px solid #ccc;
      border-radius: 5px;
      height: 300px; // Set a fixed height here
      overflow-y: auto; // Enable vertical scrolling
    }
  
    .item-list {
      width: 30%;
    }
  
    .item {
      cursor: pointer;
      display: block;
      width: 100%;
    }
  
    .item-details {
      width: 70%;
      min-height: 300px;
      box-sizing: border-box;
      border-left: 1px solid #ccc;
  
      div {
        h2 {
          margin-top: 2rem;
        }
      }
    }
  }
  </style>
  